@use "vars" as *;

body {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-style: normal;
}

h1, h2, h3, h5 {
    font-family: $font-primary;
}

h1 {
  font-size: 140px;
  @media (max-width: 768px) {
    font-size: 55px;
  }
}

h2 {
  font-size: 70px;
  @media (max-width: 768px) {
    font-size: 40px;
  }
  &.sub {
    font-size: 96px;
    margin-top: -20px;
    @media (max-width: 768px) {
      margin-top: 0px;
      font-size: 45px;
    }
  }
}

h3 {
  font-size: 33px;
  @media (max-width: 768px) {
    font-size: 25px;
  }
}

h4 {
    font-weight: 300;
    font-size: 24px;
    @media (max-width: 768px) {
      font-size: 18px;
    }
}

h5 {
    font-weight: 300;
    font-size: 24px;
    @media (max-width: 768px) {
      font-size: 20px;
    }
}

p {
  font-size: 20px;
  @media (max-width: 768px) {
    font-size: 17px;
  }
  &.small {
    font-size: 12px;
    letter-spacing: 4px;
  }
}

.f-primary {
  font-family: $font-primary;
}

.f-light {
  font-family: $font-light;
}

.f-imogen {
  font-family: $font-imogen;
}

.f-bohem {
  font-family: $font-bohem;
}
