@use "vars" as *;

.caps {
  text-transform: uppercase;
}

.green {
  background-color: $color-primary;
  color: $color-secondary;
  p, td {
    a {
      border-bottom: 1px dotted $color-secondary;
    }
  }
}

.cream {
  background-color: $color-secondary;
  color: $color-primary;
  p, td {
    a {
      border-bottom: 1px dotted $color-primary;
    }
  }
}

.white {
    background-color: white;
    color: $color-primary;
    p, td {
      a {
        border-bottom: 1px dotted $color-primary;
      }
    }
  }

.heading {
  margin-bottom: 100px;
  &-sml {
    margin-bottom: 50px;
    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
  }
  @media (max-width: 768px) {
    margin-bottom: 40px;
  }
}

.mb-20 {
    margin-bottom: 20px;
  }

  .mb-40 {
    margin-bottom: 40px;
  }

  .mb-10 {
    margin-bottom: 10px;
  }

.center {
  text-align: center;
}

.border-right {
  border-right: 1px dashed #ffffff0d;
  @media (max-width: 1024px) {
    border: 0;
  }
}

.border-left {
  border-left: 1px dashed #ffffff0d;
  @media (max-width: 1024px) {
    border: 0;
  }
}

.col-holder {
  display: flex;
  @media (min-width: 768px) and (max-width: 1024px) {
    gap: 50px;
  }
  @media (max-width: 768px) {
    flex-direction: column;
  }
  &.spacer-200 {
    margin-bottom: 200px;
    @media (max-width: 768px) {
      margin-bottom: 50px;
    }
  }

  .col-1 {
    width: 50%;
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  .col-2 {
    width: 50%;
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
}

.wedding1 {
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% !important;
  width: 500px;
  height: 500px;
  object-fit: cover;
  margin: 0 auto;
  @media (max-width: 768px) {
    height: 350px;
    margin-top: 30px;
  }
}

.wedding2 {
  border-radius: 68% 32% 40% 60% / 30% 30% 70% 70% !important;
  width: 500px;
  height: 500px;
  object-fit: cover;
  margin: 0 auto;
  @media (max-width: 768px) {
    height: 350px;
    margin-top: 30px;
  }
}

.wedding3 {
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% !important;
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin: 30px auto 0;
  display: block;
}