// _hero.scss
.hero-section {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  @media (max-width: 768px) {
    height: 600px;
  }

  .hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: opacity 2s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(
        0,
        0,
        0,
        0.3
      ); // Slight overlay for better text visibility
    }

    &.image-1 {
      opacity: 1;
      background-image: url("../assets/images/lake.jpg");
    }

    &.image-2 {
      opacity: 0;
      background-image: url("../assets/images/ktima.jpg");
    }
  }

  .hero-content {
    position: relative;
    z-index: 10;
    text-align: center;
    color: #fff;
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    &-holder {
      text-shadow: 1px 1px 5px black;
    }

    .cta-button {
      padding: 0.75rem 2rem;
      background-color: rgba(255, 255, 255, 0.8);
      color: #333;
      border: none;
      transition: all 0.3s ease;
      font-size: 1rem;
      letter-spacing: 2px;

      &:hover {
        background-color: #fff;
        transform: translateY(-2px);
      }
    }
  }
}
