// Accordion styles
.accordion {
  width: 100%;
  margin: 0 auto;

  &-item {
    margin-bottom: 15px;
    overflow: hidden;
    border-bottom: 1px dotted #3e4e3c;

    &.active {
      .accordion-header::after {
        transform: translateY(-50%) rotate(45deg);
      }

      .accordion-content {
        max-height: 1000px; // large value to accommodate any content
        padding: 20px 0 20px 0;
      }
    }
  }

  &-header {
    position: relative;
    padding: 18px 0px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;

    // Plus icon
    &::after {
      content: "+";
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 24px;
      transition: transform 0.3s ease;
    }

  }

  &-content {
    max-height: 0;
    padding: 0 0px;
    overflow: hidden;
    transition: max-height 0.3s ease, padding 0.3s ease;

    p {
      margin-top: 0;
      line-height: 1.6;

      &:last-child {
        margin-bottom: 0;
      }
    }

    img {
      max-width: 100%;
      height: auto;
      margin: 10px 0;
      border-radius: 4px;
    }
  }
}