@use 'vars' as *;

.site-header {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 100;
  transition: all 0.3s ease;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0rem;
    max-width: 96%;
    margin: 0 auto;
  }

  .logo {
    img {
        display: inline-block;
        height: 80px;
        @media (max-width: 768px) {
          height: 65px;
        }
    }
  }

  .nav-links {
    display: flex;
    list-style: none;

    li {
      margin-left: 2rem;
      font-size: 20px;

      a {
        text-decoration: none;
        color: white;
        transition: color 0.2s;
        text-shadow: 1px 1px 5px black;
        @media (max-width: 768px) {
          text-shadow: none;
        }

        &:hover {
          color: rgba(255, 255, 255, 0.5); // Some elegant wedding-appropriate color
        }
      }
    }
  }

  .hamburger-menu {
    display: none;
  }
}

// Mobile styles in _mobile.scss
@media (max-width: 768px) {
  .site-header {
    .nav-links {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      background: #3e4e3c;
      flex-direction: column;
      padding: 2rem;
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
      transform: translateY(-150%);
      transition: transform 0.3s ease;

      &.active {
        transform: translateY(0);
      }

      li {
        margin: 1rem 0;
        a {
          color: #dcd7c1;
        }
      }
    }

    .hamburger-menu {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 24px;
      background: none;
      border: none;
      cursor: pointer;
      position: relative;
      z-index: 999;

      span {
        display: block;
        width: 30px;
        height: 2px;
        background: #fff;
        transition: all 0.3s ease;
      }

      &.active {
        span:nth-child(1) {
          transform: rotate(45deg) translate(6px, 9px);
          background: #dcd7c1;
        }

        span:nth-child(2) {
          opacity: 0;
        }

        span:nth-child(3) {
          transform: rotate(-45deg) translate(6px, -9px);
          background: #dcd7c1;
        }
      }
    }
  }
}
