// Reset margins, padding, and box-sizing for all elements
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  // Set base font settings
  html {
    font-size: 16px;
    scroll-behavior: smooth;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    line-height: 1.6;
    color: #333;
    background-color: #fff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  // Reset lists
  ul,
  ol {
    list-style: none;
  }
  
  // Reset links
  a {
    text-decoration: none;
    color: inherit;
  }
  
  // Make images and other embedded content responsive
  img,
  video,
  iframe {
    max-width: 100%;
    height: auto;
    display: block;
  }
  
  // Reset form elements
  button,
  input,
  select,
  textarea {
    font: inherit;
    color: inherit;
    border: none;
    background: none;
  }
  
  button {
    cursor: pointer;
  }
  
  // Remove default outline styles, but be mindful of accessibility
  :focus {
    outline: 2px solid #a87d5d;
    outline-offset: 2px;
  }
  
  // Accessibility: provide a way to skip outlines for mouse users
  :focus:not(:focus-visible) {
    outline: none;
  }
  
  // Fix some odd inconsistencies
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: normal;
    line-height: 1.2;
  }
  
  // Remove text-size adjustments in certain browsers
  html {
    -webkit-text-size-adjust: 100%;
  }
  
  // Make sure tables retain borders and spacing
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }